











import { Vue, Component, Prop } from "vue-property-decorator";
import { Marble, PositionInBoard } from "@/types/types";

const STEP_WIDTH = 40;

@Component
export default class MarbleComponent extends Vue {
  @Prop({ type: Object as () => Marble })
  public model!: Marble;

  getWrapperStyle() {
    return {
      transform: `
        translateX(${(this.model.column - 1) * (STEP_WIDTH + 4) + "px"})
        translateY(${(this.model.row - 1) * (STEP_WIDTH + 4) + "px"})
        `
    };
  }

  getMarbleClasses() {
    const {isMoving, countOnPlace} = this.model;
    if (isMoving) {
      return []
    }
    
    return [
      {
        [`multiple-${countOnPlace}`]: countOnPlace > 1,
        multiple: countOnPlace > 1
      }
    ];
  }

  getMarbleStyle() {
    let style: any = {};
    const countOnPlace: number = this.model.countOnPlace || 1;
    const side = this.model.side;

    if (countOnPlace >= 2) {
      if (side === 1) {
        style.left = 0;
        style.bottom = 0;
      } else if (side === 2) {
        style.left = 0;
        style.top = 0;
      } else if (side === 3) {
        style.right = 0;
        style.top = 0;
      } else if (side === 4) {
        style.right = 0;
        style.bottom = 0;
      }
    }
    return style;
  }

  onClickMarble() {
    this.$emit("clickmarble", this.model);
  }
}
